.tu-button-group {
  display: inline-block;
  vertical-align: middle;
  margin: auto;
  margin-top: -2px;
  box-shadow: 3px 3px 6px #c8d0e7, -2px -2px 5px #ffffff;
  border-radius: 4px; }
  .tu-button-group--default {
    box-shadow: 3px 3px 6px #c8d0e7, -2px -2px 5px #ffffff; }
    .tu-button-group--default .tu-button:active {
      box-shadow: inset 2px 2px 5px #c8d0e7, inset -2px -2px 5px #ffffff; }
  .tu-button-group--primary {
    transition: 0.1s ease;
    background: #6d5dfc;
    box-shadow: inset 2px 2px 10px #8abdff, inset -2px -2px 10px #510ccf, 3px 3px 6px #c8d0e7, -2px -2px 5px #ffffff;
    color: #e4ebf5; }
    .tu-button-group--primary .tu-button:hover {
      color: #ffffff; }
    .tu-button-group--primary .tu-button:active {
      box-shadow: inset 2px 2px 10px #510ccf, inset -2px -2px 10px #8abdff; }
  .tu-button-group--success {
    transition: 0.1s ease;
    background: #6bc43f;
    box-shadow: inset 2px 2px 10px #93e769, inset -2px -2px 10px #3d8618, 3px 3px 6px #c8d0e7, -2px -2px 5px #ffffff;
    color: #e4ebf5; }
    .tu-button-group--success .tu-button:hover {
      color: #ffffff; }
    .tu-button-group--success .tu-button:active {
      box-shadow: inset 2px 2px 10px #3d8618, inset -2px -2px 10px #93e769; }
  .tu-button-group--info {
    transition: 0.1s ease;
    background: #909399;
    box-shadow: inset 2px 2px 10px #c5c5c5, inset -2px -2px 10px #54585f, 3px 3px 6px #c8d0e7, -2px -2px 5px #ffffff;
    color: #e4ebf5; }
    .tu-button-group--info .tu-button:hover {
      color: #ffffff; }
    .tu-button-group--info .tu-button:active {
      box-shadow: inset 2px 2px 10px #54585f, inset -2px -2px 10px #c5c5c5; }
  .tu-button-group--warning {
    transition: 0.1s ease;
    background: #e6a23c;
    box-shadow: inset 2px 2px 10px #ffc979, inset -2px -2px 10px #b67617, 3px 3px 6px #c8d0e7, -2px -2px 5px #ffffff;
    color: #e4ebf5; }
    .tu-button-group--warning .tu-button:hover {
      color: #ffffff; }
    .tu-button-group--warning .tu-button:active {
      box-shadow: inset 2px 2px 10px #b67617, inset -2px -2px 10px #ffc979; }
  .tu-button-group--danger {
    transition: 0.1s ease;
    background: #f56c6c;
    box-shadow: inset 2px 2px 10px #ff9f9f, inset -2px -2px 10px #c44444, 3px 3px 6px #c8d0e7, -2px -2px 5px #ffffff;
    color: #e4ebf5; }
    .tu-button-group--danger .tu-button:hover {
      color: #ffffff; }
    .tu-button-group--danger .tu-button:active {
      box-shadow: inset 2px 2px 10px #c44444, inset -2px -2px 10px #ff9f9f; }
  .tu-button-group > .tu-button {
    float: left;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0;
    box-shadow: none;
    transition: none;
    background-color: transparent; }
    .tu-button-group > .tu-button + .tu-button {
      margin-left: 0; }
    .tu-button-group > .tu-button.is-disabled {
      z-index: 1; }
    .tu-button-group > .tu-button:first-child {
      border-top-right-radius: 0;
      border-bottom-right-radius: 0; }
    .tu-button-group > .tu-button:last-child {
      border-top-left-radius: 0;
      border-bottom-left-radius: 0; }
    .tu-button-group > .tu-button:first-child:last-child {
      border-top-right-radius: 4px;
      border-bottom-right-radius: 4px;
      border-top-left-radius: 4px;
      border-bottom-left-radius: 4px; }
      .tu-button-group > .tu-button:first-child:last-child.is-round {
        border-radius: 20px; }
      .tu-button-group > .tu-button:first-child:last-child.is-circle {
        border-radius: 50%; }
    .tu-button-group > .tu-button:not(:first-child):not(:last-child) {
      border-radius: 0; }
    .tu-button-group > .tu-button.is-active {
      z-index: 1; }
