.fade-in-linear-enter-active,
.fade-in-linear-leave-active {
  transition: opacity 200ms linear; }

.fade-in-linear-enter,
.fade-in-linear-leave,
.fade-in-linear-leave-active {
  opacity: 0; }

/** tu-fade-in-linear */
.tu-fade-in-easeout-enter-active,
.tu-fade-in-easeout-leave-active {
  transition: opacity 0.3s ease-out; }

.tu-fade-in-easeout-enter,
.tu-fade-in-easeout-leave,
.tu-fade-in-easeout-leave-active {
  opacity: 0; }

/** tu-fade-in */
.tu-fade-in-enter-active,
.tu-fade-in-leave-active {
  transition: all 0.3s cubic-bezier(0.55, 0, 0.1, 1); }

.tu-fade-in-enter,
.tu-fade-in-leave-active {
  opacity: 0; }

/** tu-popper-in-center */
.tu-popper-in-center-enter-active,
.tu-popper-in-center-leave-active {
  transition: all 0.2s cubic-bezier(0.55, 0, 0.1, 1); }

.tu-popper-in-center-enter,
.tu-popper-in-center-leave-active {
  scale: 0;
  transform: scaleX(0); }

/** tu-zoom-in-center */
.tu-zoom-in-center-enter-active,
.tu-zoom-in-center-leave-active {
  transition: all 0.3s cubic-bezier(0.55, 0, 0.1, 1); }

.tu-zoom-in-center-enter,
.tu-zoom-in-center-leave-active {
  opacity: 0;
  transform: scaleX(0); }

/** tu-zoom-in-top */
.tu-zoom-in-top-enter-active,
.tu-zoom-in-top-leave-active {
  opacity: 1;
  transform: scaleY(1);
  transition: transform 300ms cubic-bezier(0.23, 1, 0.32, 1), opacity 300ms cubic-bezier(0.23, 1, 0.32, 1);
  transform-origin: center top; }

.tu-zoom-in-top-enter,
.tu-zoom-in-top-leave-active {
  opacity: 0;
  transform: scaleY(0); }

.tu-zoom-in-bottom-enter-active,
.tu-zoom-in-bottom-leave-active {
  opacity: 1;
  transform: scaleY(1);
  transition: transform 300ms cubic-bezier(0.23, 1, 0.32, 1), opacity 300ms cubic-bezier(0.23, 1, 0.32, 1);
  transform-origin: center bottom; }

.tu-zoom-in-bottom-enter,
.tu-zoom-in-bottom-leave-active {
  opacity: 0;
  transform: scaleY(0); }

/** tu-zoom-in-left */
.tu-zoom-in-left-enter-active,
.tu-zoom-in-left-leave-active {
  opacity: 1;
  transform: scale(1, 1);
  transition: transform 300ms cubic-bezier(0.23, 1, 0.32, 1), opacity 300ms cubic-bezier(0.23, 1, 0.32, 1);
  transform-origin: top left; }

.tu-zoom-in-left-enter,
.tu-zoom-in-left-leave-active {
  opacity: 0;
  transform: scale(0.45, 0.45); }

.collapse-transition {
  transition: 0.3s height ease-in-out, 0.3s padding-top ease-in-out, 0.3s padding-bottom ease-in-out; }

.horizontal-collapse-transition {
  transition: 0.3s width ease-in-out, 0.3s padding-left ease-in-out, 0.3s padding-right ease-in-out; }

.tu-list-enter-active,
.tu-list-leave-active {
  transition: all 0.48s; }

.tu-list-enter,
.tu-list-leave-active {
  opacity: 0;
  transform: translateX(-40px); }

.tu-cascader-menu-enter-active,
.tu-cascader-menu-leave-active {
  transition: all 0.3s; }

.tu-cascader-menu-enter,
.tu-cascader-menu-leave-active {
  opacity: 0;
  margin-right: -180px; }

.tu-opacity-transition {
  transition: opacity 0.3s cubic-bezier(0.55, 0, 0.1, 1); }

html,
body {
  height: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  background-color: #e4ebf5; }

body {
  -webkit-font-smoothing: antialiased;
  font-size: 14px;
  color: #8394be;
  -webkit-font-smoothing: antialiased; }

/* BEM [block]__[element]--[modifier]
 -------------------------- */
.tu-calendar {
  box-shadow: inset 2px 2px 5px #c8d0e7, inset -2px -2px 5px #ffffff; }
  .tu-calendar__header {
    display: flex;
    justify-content: space-between;
    padding: 12px 20px;
    border-bottom: 1px solid rgba(190, 200, 228, 0.38); }
  .tu-calendar__title {
    color: #8394be;
    font-weight: bold;
    align-self: center; }
  .tu-calendar__body {
    padding: 12px 20px 35px; }
  .tu-calendar .tu-calendar-table {
    table-layout: fixed;
    width: 100%; }
    .tu-calendar .tu-calendar-table thead th {
      padding: 12px 0;
      color: #8394be;
      font-weight: normal;
      border: none; }
    .tu-calendar .tu-calendar-table:not(.is-range) td.prev,
    .tu-calendar .tu-calendar-table:not(.is-range) td.next {
      color: #c8d0e7; }
    .tu-calendar .tu-calendar-table td {
      padding: 0;
      border-bottom: 1px solid rgba(190, 200, 228, 0.38);
      border-right: 1px solid rgba(190, 200, 228, 0.38);
      vertical-align: top;
      color: #8394be;
      transition: background-color 0.2s ease; }
      .tu-calendar .tu-calendar-table td.is-selected {
        box-shadow: inset 2px 2px 5px #c8d0e7, inset -2px -2px 5px #ffffff;
        border-bottom: 1px solid transparent;
        border-right: 1px solid transparent; }
      .tu-calendar .tu-calendar-table td.is-today {
        color: #6d5dfc;
        font-weight: bold; }
    .tu-calendar .tu-calendar-table tr:first-child td {
      border-top: 1px solid rgba(190, 200, 228, 0.38); }
    .tu-calendar .tu-calendar-table tr td:first-child {
      border-left: 1px solid rgba(190, 200, 228, 0.38); }
    .tu-calendar .tu-calendar-table tr.tu-calendar-table__row--hide-border td {
      border-top: none; }
    .tu-calendar .tu-calendar-table .tu-calendar-day {
      box-sizing: border-box;
      padding: 8px;
      height: 85px;
      transition: background-color 0.3s; }
      .tu-calendar .tu-calendar-table .tu-calendar-day:hover {
        cursor: pointer;
        background-color: rgba(255, 255, 255, 0.15); }
  .tu-calendar--card .tu-calendar__body {
    padding: 16px; }
  .tu-calendar--card .tu-calendar-table thead th {
    padding: 0; }
  .tu-calendar--card .tu-calendar-table tr {
    border-color: transparent; }
  .tu-calendar--card .tu-calendar-table td {
    border-color: transparent;
    box-shadow: none; }
    .tu-calendar--card .tu-calendar-table td.is-selected {
      box-shadow: none; }
      .tu-calendar--card .tu-calendar-table td.is-selected span {
        box-shadow: inset 2px 2px 5px #c8d0e7, inset -2px -2px 5px #ffffff; }
  .tu-calendar--card .tu-calendar-table tr:first-child td,
  .tu-calendar--card .tu-calendar-table tr td:first-child {
    border-color: transparent; }
  .tu-calendar--card .tu-calendar-table .tu-calendar-day {
    height: 20px;
    padding: 0;
    margin: 12px 0;
    display: flex;
    align-items: center;
    justify-content: center; }
    .tu-calendar--card .tu-calendar-table .tu-calendar-day > span {
      display: inline-block;
      width: 24px;
      height: 24px;
      text-align: center;
      line-height: 24px;
      transition: color 0.3s; }
      .tu-calendar--card .tu-calendar-table .tu-calendar-day > span:hover {
        color: #6d5dfc; }
